import React, { Component } from 'react';
import moment from 'moment';
import axios from "axios";

import Dashboard from './Dashboard'

export default class HomeMoving extends Component {
  constructor(props) {
    super(props)
    this.state = {
      finalBaseDone: false,
      smartGrid: {},
      deviceLocation: '',
      currentDay: 0,
      currentDate: '',
      deviceLocationMoved: 0,
      deviceLocationMovedBase: [],
      currDayIntervals: [],
      currDayIntervalsCounter: 0,
      currentDayIteration: 0,
      finalBase: []
    }
  }
  generateCurrDayIntervals() {
    let startDate = moment().set({ h: 0, m: 0, s: 0 }).valueOf() + (this.state.currDayIntervalsCounter) * 60000
    let endDate = moment().set({ h: 0, m: 5, s: 0 }).valueOf() + (this.state.currDayIntervalsCounter) * 60000
    if (moment(endDate).format('HH:mm:ss') === "00:00:00") {
      endDate = moment().set({ h: 23, m: 59, s: 59 }).valueOf()
    }
    this.setState({
      currDayIntervals: [...this.state.currDayIntervals, {
        startTime: moment(startDate).format('HH:mm:ss'),
        endTime: moment(endDate).format('HH:mm:ss')
      }],
      currDayIntervalsCounter: this.state.currDayIntervalsCounter + 5
    });
  }
  renderHouseMoveTable(response) {
    const interval = 5; //5 min interval
    const dayIntervals = (24 * 60) / interval;
    endOfMainBase:
    for (let d = this.state.currentDay; d < ((this.state.currentDay + 1) * 2); d++) {
      outer:
      for (let i = 0; i < dayIntervals; i++) {
        this.generateCurrDayIntervals()
        for (let j = this.state.currentDayIteration; j < response.data.days.length; j++) {
          if (j === response.data.days.length - 1) {
            break endOfMainBase;
          }
          const element = response.data.days[j];
          element.id = j
          const elementDate = moment(element.date).calendar()
          const elementTime = moment(element.date).format('HH:mm:ss')
          //Если новая дата то делаем что-то и обновляем дату
          if (elementDate !== this.state.currentDate) {
            if (this.state.finalBase.length === 0) {
              this.setState({
                deviceLocationMovedBase: [],
                deviceLocationMoved: 0,
                currentDate: elementDate,
                currDayIntervals: [],
                currDayIntervalsCounter: 0,
                finalBase: [...this.state.finalBase, {
                  day: this.state.currentDate,
                  data: this.state.deviceLocationMovedBase
                }],
                currentDayIteration: j
              })
              console.log(`%c -> Day render ${this.state.currentDay} ..`, 'background: #20c997; color: #000')
              break outer;
            }
            for (let k = 0; k < this.state.finalBase.length; k++) {
              const element = this.state.finalBase[k];
              if (element.day !== this.state.currentDate) {
                this.setState({
                  deviceLocationMovedBase: [],
                  deviceLocationMoved: 0,
                  currentDate: elementDate,
                  currDayIntervals: [],
                  currDayIntervalsCounter: 0,
                  finalBase: [...this.state.finalBase, {
                    day: this.state.currentDate,
                    data: this.state.deviceLocationMovedBase
                  }],
                  currentDayIteration: j
                })
                console.log(`%c -> Day render ${this.state.currentDay} ..`, 'background: #20c997; color: #000')
                break outer;
              }
            }
          }
          if (elementTime > this.state.currDayIntervals[i].startTime && elementTime < this.state.currDayIntervals[i].endTime) {
            //console.log(`%c ${this.state.currDayIntervals[i].startTime} -> ${elementTime} -> ${this.state.currDayIntervals[i].endTime}`, 'background: #ffc107; color: #000; font-weight: bold;')
            // Если локация поменялась, то инкрементируем текущее кол-во перемещений
            if (element.device.location !== this.state.deviceLocation) {
              //console.log(`%c Локация: ${element.device.location} !==  Локации: ${this.state.deviceLocation}`, 'background: #dc3545; color: #fff')
              this.setState({
                deviceLocation: element.device.location,
                deviceLocationMoved: this.state.deviceLocationMoved + 1,
                currentDayIteration: j
              })
              //console.log('%c Замечена активность: ', 'background: #222; color: #fff', this.state)
            }
            if (response.data.days[j + 1] && (moment(response.data.days[j + 1].date).calendar() !== elementDate)) {
              //console.log(`%c this ${elementDate} !== next day ${moment(response.data.days[j + 1].date).calendar()}`, 'background: #e83e8c; color: #fff')
              this.setState({
                deviceLocationMovedBase: [...this.state.deviceLocationMovedBase, {
                  count: this.state.deviceLocationMoved,
                  timeDuration: `${this.state.currDayIntervals[i].startTime} - ${this.state.currDayIntervals[i].endTime}`,
                  date: this.state.currentDate
                }],
                currentDayIteration: j + 1,
                currentDay: this.state.currentDay + 1,
              })
              break;
            }
            if (response.data.days[j + 1] === undefined) {
              this.setState({
                deviceLocationMovedBase: [...this.state.deviceLocationMovedBase, {
                  count: this.state.deviceLocationMoved,
                  timeDuration: `${this.state.currDayIntervals[i].startTime} - ${this.state.currDayIntervals[i].endTime}`,
                  date: this.state.currentDate
                }],
                deviceLocationMoved: 0,
                currentDayIteration: j
              })
              this.setState({
                deviceLocationMovedBase: [],
                deviceLocationMoved: 0,
                currentDate: elementDate,
                currDayIntervals: [],
                currDayIntervalsCounter: 0,
                finalBase: [...this.state.finalBase, {
                  day: this.state.currentDate,
                  data: this.state.deviceLocationMovedBase
                }],
                currentDayIteration: j
              })
              break outer;
            }
          }
          if (elementTime > this.state.currDayIntervals[i].endTime) {
            this.setState({
              deviceLocationMovedBase: [...this.state.deviceLocationMovedBase, {
                count: this.state.deviceLocationMoved,
                timeDuration: `${this.state.currDayIntervals[i].startTime} - ${this.state.currDayIntervals[i].endTime}`,
                date: this.state.currentDate
              }],
              deviceLocationMoved: 0,
              currentDayIteration: j
            })
            break;
          }
        }
      }
    }
  }

  async componentDidMount() {
    try {

      const response = await axios.get('./Msn3.json');
      this.setState({
        deviceLocation: response.data.days[0].device.location,
        currentDate: moment(response.data.days[0].date).calendar(),
      })
      console.log(`%c -> Day render ${this.state.currentDay} ..`, 'background: #20c997; color: #000')

      this.renderHouseMoveTable(response)
      this.setState({
        deviceLocationMovedBase: [],
        deviceLocationMoved: 0,
        currDayIntervals: [],
        currDayIntervalsCounter: 0,
        finalBase: [...this.state.finalBase, {
          day: this.state.currentDate,
          data: this.state.deviceLocationMovedBase
        }],
        smartGrid: response.data.days
      })

      console.log('RESULT: ', this.state)
      this.setState({
        finalBaseDone: true
      })

    } catch (err) {
      console.log(err)
    }
  }
  render() {
    return <Dashboard homeMoving={this.state} />
  }
}