import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';


import 'react-virtualized/styles.css'
import { Table, Column, AutoSizer } from "react-virtualized";

const styles = {
  root: {
    width: '100%',
    height: '100%',
    overflowX: 'auto',
  }
};
const rowHeight = 50;

function SimpleTable(props) {
  const { classes, targetElement } = props;

  return (
    <Paper className={classes.root}>
      <AutoSizer>
        {({ width, height }) => {
          return (

            <Table
              width={width}
              height={height}
              headerHeight={50}
              rowHeight={rowHeight}
              rowGetter={({ index }) => targetElement[index]}
              rowCount={targetElement.length}
              overscanRowCount={3} >
              <Column
                width={50}
                label='#'
                dataKey='id'
              />
              <Column
                width={200}
                label='Дата'
                dataKey='date'
                cellDataGetter={({ dataKey, rowData }) => new Date(rowData[dataKey]).toString()}
              />
              <Column
                width={100}
                label='Датчик'
                cellDataGetter={({ dataKey, rowData }) => rowData.device[dataKey]}
                dataKey='type'
              />
              <Column
                width={100}
                label='Локація'
                cellDataGetter={({ dataKey, rowData }) => rowData.device[dataKey]}
                dataKey='location'

              />
            </Table>
          )
        }}
      </AutoSizer>

    </Paper>
  );
}

SimpleTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SimpleTable);
