import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import { mainListItems } from './listItems';
import SimpleLineChart from './SimpleLineChart';
import SimpleTable from './SimpleTable';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import loading from './img/loading.gif'
import AnomalyChart from './AnomalyChart';

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing.unit * 7,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing.unit * 9,
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    height: '100vh',
    overflow: 'auto',
    position: 'relative'
  },
  chartContainer: {
    marginLeft: -22,
  },
  tableContainer: {
    height: 320,
  },
  h5: {
    marginBottom: theme.spacing.unit * 2,
  },
});

class Dashboard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      open: true,
      weekDay: 0,
      labelWidth: 0,
    }
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  shouldComponentUpdate(nextProps) {
    if (this.props.homeMoving.finalBaseDone !== nextProps.homeMoving.finalBaseDone) {
      return true
    }
    if (this.props.homeMoving.finalBaseDone) {
      return true
    } else return false

  }
  componentDidMount() {
    if (this.props.homeMoving.finalBaseDone) {
      this.setState({
        labelWidth: ReactDOM.findDOMNode(this.InputLabelRef).offsetWidth,
      });
    }

  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };
  render() {
    const { classes, homeMoving } = this.props;
    return (
      <BrowserRouter>
        <div className={classes.root}>
          <CssBaseline />
          <AppBar
            position="absolute"
            className={classNames(classes.appBar, this.state.open && classes.appBarShift)}
          >
            <Toolbar disableGutters={!this.state.open} className={classes.toolbar}>
              <IconButton
                color="inherit"
                aria-label="Open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(
                  classes.menuButton,
                  this.state.open && classes.menuButtonHidden,
                )}
              >
                <MenuIcon />
              </IconButton>
              <Typography
                component="h1"
                variant="h6"
                color="inherit"
                noWrap
                className={classes.title}
              >
                FEL | SmartGrid Watch System v. 1.0.0
            </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            variant="permanent"
            classes={{
              paper: classNames(classes.drawerPaper, !this.state.open && classes.drawerPaperClose),
            }}
            open={this.state.open}
          >
            <div className={classes.toolbarIcon}>
              <IconButton onClick={this.handleDrawerClose}>
                <ChevronLeftIcon />
              </IconButton>
            </div>
            <Divider />
            <List>{mainListItems}</List>
          </Drawer>
          <main className={classes.content}>
            {
              homeMoving.finalBaseDone
                ? (
                  <React.Fragment>
                    <Switch>
                      <Route exact path='/' render={() => (
                          <div>
                            <div className={classes.appBarSpacer} />
                            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                              <Typography variant="h4" gutterBottom component="h2">Активність за день</Typography>
                              <form className={classes.root} autoComplete="off">
                                <FormControl className={classes.formControl}>
                                  <InputLabel htmlFor="weekDay-simple">Оберіть день</InputLabel>
                                  <Select
                                    value={this.state.weekDay}
                                    onChange={this.handleChange}
                                    inputProps={{
                                      name: 'weekDay',
                                      id: 'weekDay-simple',
                                    }}
                                  >
                                    {
                                      homeMoving.finalBase.map((el, i) => {
                                        return <MenuItem key={i} value={i}>{el.day}</MenuItem>
                                      })
                                    }
                                  </Select>
                                </FormControl>
                              </form>
                            </div>
                            <Typography component="div" className={classes.chartContainer}>
                              <SimpleLineChart weekDay={this.state.weekDay} movingBase={homeMoving.finalBase} />
                            </Typography>
                            <Typography variant="h4" gutterBottom component="h2">Покази датчиків</Typography>
                            <div className={classes.tableContainer}>
                              <SimpleTable targetElement={homeMoving.smartGrid} />
                            </div>
                          </div>
                      )} />
                      <Route path='/activity' render={(props) => (
                          <div>
                            <div className={classes.appBarSpacer} />
                            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                              <Typography variant="h4" gutterBottom component="h2">Активність за день</Typography>
                              <form className={classes.root} autoComplete="off">
                                <FormControl className={classes.formControl}>
                                  <InputLabel htmlFor="weekDay-simple">Оберіть день</InputLabel>
                                  <Select
                                    value={this.state.weekDay}
                                    onChange={this.handleChange}
                                    inputProps={{
                                      name: 'weekDay',
                                      id: 'weekDay-simple',
                                    }}
                                  >
                                    {
                                      homeMoving.finalBase.map((el, i) => {
                                        return <MenuItem key={i} value={i}>{el.day}</MenuItem>
                                      })
                                    }
                                  </Select>
                                </FormControl>
                              </form>
                            </div>
                            <AnomalyChart {...props} weekDay={this.state.weekDay} movingBase={homeMoving.finalBase} />
                          </div>
                      )} />
                      <Route render={() => (
                        <div>
                          <div className={classes.appBarSpacer} />
                          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Typography variant="h4" gutterBottom component="h2">Активність за день</Typography>
                            <form className={classes.root} autoComplete="off">
                              <FormControl className={classes.formControl}>
                                <InputLabel htmlFor="weekDay-simple">Оберіть день</InputLabel>
                                <Select
                                  value={this.state.weekDay}
                                  onChange={this.handleChange}
                                  inputProps={{
                                    name: 'weekDay',
                                    id: 'weekDay-simple',
                                  }}
                                >
                                  {
                                    homeMoving.finalBase.map((el, i) => {
                                      return <MenuItem key={i} value={i}>{el.day}</MenuItem>
                                    })
                                  }
                                </Select>
                              </FormControl>
                            </form>
                          </div>
                          <Typography component="div" className={classes.chartContainer}>
                            <SimpleLineChart weekDay={this.state.weekDay} movingBase={homeMoving.finalBase} />
                          </Typography>
                          <Typography variant="h4" gutterBottom component="h2">Покази датчиків</Typography>
                          <div className={classes.tableContainer}>
                            <SimpleTable targetElement={homeMoving.smartGrid} />
                          </div>
                        </div>
                      )} />
                    </Switch>
                  </React.Fragment>

                )
                : (
                  <div className="preloader">
                    <img src={loading} alt="Loading.." />
                  </div>
                )
            }


          </main>
        </div>
      </BrowserRouter>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Dashboard);
