import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';


import 'react-virtualized/styles.css'
import { Table, Column, AutoSizer } from "react-virtualized";

const styles = {
  root: {
    width: '100%',
    height: '300px',
    overflowX: 'auto',
  }
};
const rowHeight = 50;

class AnomalyTable extends Component {
  render() {
    const { classes, targetElement } = this.props;
    console.log('AnomalyTable: ', targetElement)
    return (
      <Paper className={classes.root}>
        <AutoSizer>
          {({ width, height }) => {
            return (

              <Table
                width={width}
                height={height}
                headerHeight={50}
                rowHeight={rowHeight}
                rowGetter={({ index }) => targetElement[index]}
                rowCount={targetElement.length}
                overscanRowCount={2}
              >
                <Column
                  width={50}
                  label='#'
                  dataKey='id'
                />
                <Column
                  width={200}
                  label='Часовий інтервал'
                  dataKey='timeDuration'
                />
                <Column
                  width={200}
                  label='Кількість переміщень'
                  dataKey='count'
                />
              </Table>
            )
          }}
        </AutoSizer>
      </Paper>
    );
  }

}

AnomalyTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AnomalyTable);
