import React, {Component} from 'react';
import ResponsiveContainer from 'recharts/lib/component/ResponsiveContainer';
import LineChart from 'recharts/lib/chart/LineChart';
import Line from 'recharts/lib/cartesian/Line';
import XAxis from 'recharts/lib/cartesian/XAxis';
import YAxis from 'recharts/lib/cartesian/YAxis';
import CartesianGrid from 'recharts/lib/cartesian/CartesianGrid';
import Tooltip from 'recharts/lib/component/Tooltip';
import Legend from 'recharts/lib/component/Legend';
import ReferenceLine from 'recharts/lib/cartesian/ReferenceLine';
import AnomalyTable from './AnomalyTable';
import AnomalyFinder from './anomalyFinder';


export default class AnomalyChart extends Component {
  constructor(props) {
    super(props)
    this.state = {
      anomalyDatabase: [],
      anomalySearchDone: false,
      daysMiddle: 0
    }
  }



  searchAnomalyInData(props) {
    const { movingBase, weekDay } = props;
    let anomalyBase = [];
    let weekBigData = [];
    let count = 1;
    const af = new AnomalyFinder();
    for (let elem = 0; elem < movingBase.length; elem++) {
      const element = movingBase[elem];
      element.data.forEach(interval => {
        weekBigData.push(interval.count)
      });

    }
    const sum = weekBigData.reduce(add, 0);
    function add(a, b) {
      return a + b;
    }
    this.setState({
      daysMiddle: (sum / (weekBigData.length - 1)) * 10
    })
    const normalValues = weekBigData;
    movingBase[weekDay].data.forEach(el => {

      if(af.find(normalValues, el.count)) {
        anomalyBase.push({
          id: count,
          timeDuration: el.timeDuration,
          count: el.count
        })
        count += 1
      }
    })
    this.setState({
      anomalyDatabase: anomalyBase,
      anomalySearchDone: true
    })
  }
  componentDidMount(){
    this.searchAnomalyInData(this.props)
  }
  componentWillReceiveProps(nextProps){
    this.searchAnomalyInData(nextProps)
  }
  
  render(){
    const { movingBase, weekDay } = this.props;
    return (
      <React.Fragment>
        <ResponsiveContainer width="99%" height={320}>
          <LineChart data={movingBase[weekDay].data}>
            <XAxis dataKey="timeDuration" />
            <YAxis />
            <CartesianGrid vertical={false} strokeDasharray="3 3" />
            <Tooltip />
            <Legend />
            <ReferenceLine y={this.state.daysMiddle} label="Приблизна аномальна межа" stroke="red" />
            <Line name="Кількість переміщень" type="monotone" dataKey="count" dot={{ stroke: '#3f51b5', strokeWidth: 2 }} />
          </LineChart>
        </ResponsiveContainer>
        <div>
          {
            this.state.anomalySearchDone ? <p style={{ fontWeight: '700' }}>Знайдено аномалій: <span style={{color: 'red'}}>{this.state.anomalyDatabase.length}</span></p> : null
          }
        </div>
        {(this.state.anomalyDatabase.length) ? <AnomalyTable targetElement={this.state.anomalyDatabase} /> : null}
       
      </React.Fragment>
    );
  }
}
